export class GenericCameraSettingsFunctions {
  public static toFormGroup(questions: GenericCameraSettings[]) {
    const group: any = {};
    questions.forEach((question) => this.ConvertToFormGroup(group, question));
    return new FormGroup(group);
  }
  public static ConvertToFormGroup(group: any, question: GenericCameraSettings) {
    group[question.propertyName] = new FormControl(question.value || '', question.validations);
  }
}

