export const WifiModePropertyNames = Object.values(PropertyNames)
  .filter(
    (x) =>
      x[0] === PropertyNames.WifiConnectionMode[0] ||
      x[0] === PropertyNames.WIFI_ConnectTimeout[0] ||
      x[0] === PropertyNames.Ssid[0] ||
      x[0] === PropertyNames.APPassword[0] ||
      x[0] === PropertyNames.STPassword[0] ||
      x[0] === PropertyNames.APCameraIP[0] ||
      x[0] === PropertyNames.STA_AutoGetIP[0] ||
      x[0] === PropertyNames.UpdateCertificate[0] ||
      x[0] === PropertyNames.WifiDirectPin[0]
  )
  .map((val: string) => val[0]);

