import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';

import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { faTimes } from '@fortawesome/free-solid-svg-icons';

import { GenericCameraSettings } from 'src/app/modules/shared/models/camera-settings.model';
import { Organization, Role } from './selection-dialog.model';

import { OrganizationService } from 'src/app/modules/streaming/reports/reports.service';
import { RoleService } from 'src/app/modules/shared/services/role.service';

@Component({
  standalone: false,
  selector: 'app-selection-dialog',
  templateUrl: './selection-dialog.component.html',
  styleUrl: './selection-dialog.component.scss'
})
export class SelectionDialogComponent {
  UndoClick = new EventEmitter();

  ModifiedProps: GenericCameraSettings[] = [];
  organizations: Organization[] = [];
  selectedOrganizationId: number = 1;
  selectedRoleId: number = 1;
  roles: Role[] = [];
  updateType: string = 'Organization';
  faTimes = faTimes;
  userId: number = 0;
  userName: string = '';

  constructor(
    @Inject(OrganizationService) public organizationsService: OrganizationService,
    @Inject(RoleService) public roleService: RoleService,
    public dialogRef: MatDialogRef<SelectionDialogComponent>
  ) {
    this.organizationsService.getOrganizations().subscribe((data: Organization[]) => {
      data = data.sort((a, b) => (a.CompanyId > b.CompanyId ? 1 : -1));
      this.organizations = data;
    });

    this.roleService.getRoles().subscribe((data: any) => {
      data = data.sort((a: any, b: any) => (a.ID > b.ID ? 1 : -1));
      this.roles = data;
    });

    this.dialogRef.backdropClick().subscribe(() => {
      // Close the dialog
      dialogRef.close();
    });
  }

  onOrganizationChange($event: any) {
    this.selectedOrganizationId = $event.value;
  }

  onRoleChange($event: any) {
    this.selectedRoleId = $event.value;
  }

  onTypeChange($event: any) {
    console.log($event.value);
    this.updateType = $event.value;
  }

  closeDialog() {
    this.dialogRef.close({
      Status: false,
      OrganizationId: this.selectedOrganizationId,
      OrganizationName: this.organizations.find((x) => x.CompanyId == this.selectedOrganizationId)!.Name,
      RoleId: this.selectedRoleId,
      RoleName: this.roles.find((x) => x.ID == this.selectedRoleId)!.RoleName,
      UpdateType: this.updateType
    });
  }

  onSuccessClick() {
    this.dialogRef.close({
      Status: true,
      OrganizationId: this.selectedOrganizationId,
      OrganizationName: this.organizations.find((x) => x.CompanyId == this.selectedOrganizationId)!.Name,
      RoleName: this.roles.find((x) => x.ID == this.selectedRoleId)!.RoleName,
      RoleId: this.selectedRoleId,
      UpdateType: this.updateType
    });
  }

  resetProp(propertyName: string) {
    this.UndoClick.emit(propertyName);
    this.ModifiedProps = this.ModifiedProps.filter((x) => x.propertyName != propertyName);
  }
}
