import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BaseApiService } from 'src/app/api';
import { CameraTypes, ExternalCameraSetting, ApiMode } from '../../shared/models/camera-settings.model';

@Injectable({
  providedIn: 'root'
})
export class CameraSettingService extends BaseApiService {
  constructor(private http: HttpClient) {
    super();
  }
  getCameraSettings(Id: number, mode: ApiMode, cameraType: CameraTypes) {
    let apiCameraNumber: number = 0;
    let cameraName: string = '';
    switch (cameraType) {
      case CameraTypes.Matrix:
        apiCameraNumber = 4;
        cameraName = 'Matrix';
        break;
      case CameraTypes.Matrix2:
        apiCameraNumber = 5;
        cameraName = 'Matrix2';
        break;
      case CameraTypes.Matrix2Plus:
        apiCameraNumber = 7;
        cameraName = 'Matrix2Plus';
        break;
      case CameraTypes.Matrix4G:
        apiCameraNumber = 8;
        cameraName = 'Matrix4G';
        break;
    }
    console.log('making http request ');
    let url = this.basePath + `/api/Kiosk/GetCameraSettingsByUserId?id=${Id}&Modeid=${mode}&provider=${cameraType}`;
    return this.http.get(url);
  }
  updateCameraSettings(cameraSetting: ExternalCameraSetting) {
    return this.http.post(this.basePath + '/api/Kiosk/UpdateCameraSettings', cameraSetting);
  }
}
